$primary:rgb(230,230,250);
$secondary:rgb(25,25,112);
$tertiary:rgb(0,128,128);
$white:rgb(226,226,226);
$black:rgb(40,40,40);

body{
    background-color:$primary;
}

body,
input[type="text"],
textarea,
input[type="submit"],
.theCount,
button{
    color:$black;
}

h1{
    text-shadow: -0.5px -0.5px 0 white, 0.5px -0.5px 0 white, -0.5px 0.5px 0 white, 0.5px 0.5px 0 white;
}

.entry{
    background-color:rgba(25,25,112,0.1);
    border: 3px solid $secondary;
}

.charCount{
    background-color:$black;
    color:$white;
    border:1px solid $white;
}

footer{
    background-color:$secondary;
}

footer p,
footer a{
    color:$white;
}